<template>
  <a-layout>
    <a-layout-content style="min-width: 1200px;margin-right: auto;margin-left: auto;padding: 0 50px">
      <a-breadcrumb style="margin: 16px 0">
        <a-breadcrumb-item>首页</a-breadcrumb-item>
        <a-breadcrumb-item>企业资质</a-breadcrumb-item>
        <a-breadcrumb-item>修改企业资质</a-breadcrumb-item>
      </a-breadcrumb>
      <div :style="{ background: '#fff', padding: '24px', minHeight: '280px' }">
        <a-row type="flex" justify="center">
          <a-col :span="18">
            <a-form id="components-form-demo-validate-other" @submit="handleSubmit" :form="form">
              <a-form-item label="提交说明" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">请提交图片或PDF格式的文件</span>
              </a-form-item>
              <a-form-item label="必填信息" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">只有通过必填资料才能够发布用工需求哦</span>
              </a-form-item>
              <a-form-item label="企业名称" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                <!-- <span class="ant-form-text">{{entInfo.enterName}}</span> -->
                <a-input
                    style="width: 100%"
                    :placeholder="entInfo.enterName"
                    name="enterName"
                    v-decorator="[
                      'enterName',
                      {rules: [{ required: true, message: '请输入企业名称' }]}
                    ]"
                  />
              </a-form-item>
              <a-list rowKey="id" :grid="{gutter: 64, column: 4}" :dataSource="dataRequired">
                <a-list-item slot="renderItem" slot-scope="item">
                  <template>
                    <a-form-item >
                      <div class="dropbox">
                        <a-upload-dragger
                          v-decorator="[
                            '{{item.propName}}',
                            {
                              valuePropName: '{{item.propName}}',
                              getValueFromEvent: normFile,

                            }
                          ]"
                          v-bind:name=item.propName
                          action="http://123.56.250.127:80/api/user/entInfoPost"
                          :data="{id:id}"
                        >
                          <p class="ant-upload-drag-icon">
                            <a-icon type="file" />
                          </p>
                          <p class="ant-upload-text">{{item.name}}</p>
                        </a-upload-dragger>
                      </div>
                    </a-form-item>
                  </template>
                </a-list-item>
                <a-form-item label="选填信息" :labelCol="{ span: 2 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">完善选填资料能够帮助加快审核时间哦</span>
              </a-form-item>
              </a-list>
              <a-list rowKey="id" :grid="{gutter: 64, column: 4}" :dataSource="dataSelected">
                <a-list-item slot="renderItem" slot-scope="item">
                  <template>
                    <a-form-item >
                      <div class="dropbox">
                        <a-upload-dragger
                          v-decorator="[
                            '{{item.name}}',
                            {
                              valuePropName: '{{item.propName}}',
                              getValueFromEvent: normFile,
                            },
                          ]"
                          v-bind:name=item.propName
                          action="http://123.56.250.127:80/api/user/entInfoPost"
                          :data="{id:id}"
                        >
                          <p class="ant-upload-drag-icon">
                            <a-icon type="file" />
                          </p>
                          <p class="ant-upload-text">{{item.name}}</p>
                        </a-upload-dragger>
                      </div>
                    </a-form-item>
                  </template>
                </a-list-item>
              </a-list>
              <a-form-item :wrapperCol="{ span: 24 }" style="text-align: center">
                <a-button htmlType="submit" type="primary" @click="postBasicInfo">确认修改</a-button>
              </a-form-item>
            </a-form>
          </a-col>
        </a-row>
      </div>
    </a-layout-content>
    <a-layout-footer style="text-align: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
  </a-layout>
</template>

<script>
import {getEnterpriseInfoById} from '../../api/manRequest'
import { postEntQualificationRequest } from '../../api/login';
export default {
  components: {},
  data() {
    return {
      id: localStorage.getItem('userId'),
      entInfo:{},
      dataRequired: [
        {
          name: "营业执照",
          propName: "businessLicense"
        },
        {
          name: "建筑资质",
          propName: "constructionQUAL"
        },
        {
          name: "安全生产许可证",
          propName: "securityLicense"
        },
        {
          name: "社保缴费证明",
          propName: "socialSecurityCert"
        },
        {
          name: "拟用工项目中标通知书或其他文件",
          propName: "noticeOfBid"
        },
        {
          name: "项目商业保险",
          propName: "businessItemInsurance"
        },
        {
          name: "无纳税异常声明",
          propName: "noTaxExpStatement"
        }
      ],
      dataSelected: [
        {
          name: "土地使用证",
          propName: "landUseCert"
        },
        {
          name: "规划许可证",
          propName: "planningPermit"
        },
        {
          name: "施工许可证",
          propName: "constructionPermit"
        },
        {
          name: "开工报告",
          propName: "startReport"
        }
      ],
      form : this.$form.createForm(this)
    };
  },
  mounted() {
    this.getEnterpriseInfoById()
    this.$parent.showMenu=true
  },
  beforeCreate() {
  },
  methods: {
    getEnterpriseInfoById(){
      getEnterpriseInfoById(this.id)
      .then(response => {
        this.entInfo = response.data.data;
        console.log(this.entInfo);
      })
      .catch(err => {
        console.log(err);
      })
    },
    handleSubmit(e) {
      e.preventDefault();
      const { form: { validateFields } } = this
      validateFields((err, values) => {
        if (!err) {
          const entQuaValue = { enterName:values.enterName,id:this.id }
          const that = this;
          postEntQualificationRequest(entQuaValue)
            .then(response => {
              if (response.data.result === 25) {
                alert('提交成功')
                that.$router.push({ path: "/entProfile" });
              }
            })
            .catch(err => {
              console.log(err)
            })
        }
      });
    },
    normFile(e) {
      // console.log("Upload event:", e);
      if (Array.isArray(e)) {
        return e;
      }
      return e && e.fileList;
    },
  }
};
</script>

<style lang="less" scoped>
.card-list {
  /deep/ .ant-card-body:hover {
    .ant-card-meta-title > a {
      color: #f5222d;
    }
  }

  /deep/ .ant-card-meta-title {
    margin-bottom: 12px;

    & > a {
      display: inline-block;
      max-width: 100%;
      color: rgba(0, 0, 0, 0.85);
    }
  }

  /deep/ .meta-content {
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    height: 64px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;

    margin-bottom: 1em;
  }
}

.card-avatar {
  width: 48px;
  height: 48px;
  border-radius: 48px;
}

.ant-card-actions {
  background: #f7f9fa;

  li {
    float: left;
    text-align: center;
    margin: 12px 0;
    color: rgba(0, 0, 0, 0.45);
    width: 50%;

    &:not(:last-child) {
      border-right: 1px solid #e8e8e8;
    }

    a {
      color: rgba(0, 0, 0, 0.45);
      line-height: 22px;
      display: inline-block;
      width: 100%;
      &:hover {
        color: #f5222d;
      }
    }
  }
}

.new-btn {
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 188px;
}

.vcenter {
  position: absolute;
  left: 50%;
  top: 50%;
}

#components-form-demo-validate-other .dropbox {
  height: 180px;
  line-height: 1.5;
}
</style>




